<script>
	import Title from './title.vue'
	import AddNft from '../addNft/addNft.vue'
	export default {
		data() {
			return {
				willStart:[],//即将开始
				time:"00:00:00:00", //倒计时
				swiper:[
					{image:require("../../static/img/swiper.jpg"),title: 'HOLY LAND',},
					{image:'https://cdn.uviewui.com/uview/swiper/swiper1.png',title: 'HOLY LAND',},
					{image:require("../../static/img/swiper.jpg"),title: 'HOLY LAND',}
				],
				percentage:"30", 
			}
		},
		onLoad() {
		  this.goNTH()
		},
		
		methods: {
			// 即将开始
			goNTH(){
				this.$my.get('/index/purchase/get_purchase').then(res=>{
					if(res.code==200){
						let date=new Date(res.data.end_time);
						 this.willStart=res.data
						 // 获取当前与结束时间戳来计算天数
						 let timestamp = Date.parse(new Date())/1000
						 let endtime = date.getTime()/1000
						 let num = endtime-timestamp
						let timer= setInterval(()=>{
							 num--
							 let second = Math.floor(num);
							 // 天数
							 let day = Math.floor(second / 3600 / 24);
							 // 小时
							 let hr = Math.floor(second / 3600 % 24);
							 // 分钟
							 let min = Math.floor(second / 60 % 60);
							 // 秒
							 let sec = Math.floor(second % 60);
							 this.time=day + ":" + hr + ":" + min + ":" + sec
						 },1000)
						 if(num<0){
							 this.time=false
							 clearInterval(timer)
						 } 
					}
				})
			},
			// 搜索
			Gotosearch(){
				this.$my.go('/pages/index/search/search')
			}
		},
		components:{Title,AddNft}
	}
</script>

